import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { navigate } from 'gatsby';
import React from 'react';
import './header.scss';

export interface Props {
  classes: {
    appBar: string;
    menu: string;
    left: string;
    right: string;
    logo: string;

  };
}

const Header = ({ classes }: Props): React.ReactElement => (
  <AppBar className={classes.appBar} position="static">
    <Toolbar className="menu">
      <div className={classes.left}>
        <Typography variant="h6" color="inherit" className={classes.logo}>
          SEWAGE
        </Typography>
      </div>
      <div className={classes.right}>
        <Button color="inherit" size="large" onClick={(): void => { navigate('/'); }}>首页</Button>
        <Button color="inherit" size="large" onClick={(): void => { window.open('https://google.com', '_blank'); }}>关于我们</Button>
      </div>
    </Toolbar>
  </AppBar>
);

export default withStyles({})(Header);
